@use '../base/mixins' as m;
@use '../base/variables' as v;
.contenedor-badge{
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.manga{
    display: flex;
    align-items: center;
    justify-content: space-around;
    .manga-container{
        width: 80rem;
        height: 40rem;
    }
    .manga-descripcion{
        width: 50%;
        height: 100%;
        position: relative;
        
        .manga-sinopsis{
            line-height: 1.5;
        }
        .contenedor-detalle{
            li{
                display: block;
                margin-top: 1rem;
                padding: 2rem ;
                color: #fff;
            }
        }
        .contenedor-precio{
            .btn-comprar{
                transition: all 0.5s ease-in-out;
                &:hover{
                    transform: scale(1.1);
                }
            }
        }
    }
}
.bg-azul{
    background-color: v.$background-search-input;
    li:nth-child(odd){
        background-color: darken(v.$background-search-input, 15%);
    }
}
.bg-verde{
    background-color: v.$text-color;
    li:nth-child(odd){
        background-color: darken(v.$text-color, 10%);
    }
}