.contact-container {
  background-image: url(../img/logo.webp);
  background-size: cover;
  height: 100vh;
}
.contact-container .right {
  position: absolute;
  width: 36rem;
  left: 60%;
  height: 58rem;
  top: calc(50% - 29rem);
  background-color: white;
  border-radius: 2.5rem;
  text-align: center;
}
.contact-container .right .title {
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 2.5rem;
  line-height: 14.5rem;
  align-items: center;
  color: #5754dd;
}
.contact-container .right input[type=email],
.contact-container .right input[type=password],
.contact-container .right input[type=text] {
  border: none;
  outline: none;
  background: none;
  font-size: 2rem;
  color: #85b6ff;
  padding: 2rem 1rem 2rem 0.5rem;
  border: 0.2rem solid #568aee;
  width: 28rem;
  height: 4.5rem;
  margin: 1.5rem;
  border-radius: 2.5rem;
  box-sizing: border-box;
  text-align: center;
  overflow: hidden;
}
.contact-container .right input[type=checkbox] {
  vertical-align: middle;
  margin-right: 0.3rem;
  margin-bottom: 0.3rem;
  font-weight: bold;
  height: 1.8rem;
  width: 1.8rem;
}
.contact-container .right .keepme {
  margin: 1.5rem;
  color: #71a1bd;
  font-weight: bold;
}
.contact-container .right .button {
  width: 26rem;
  height: 4.5rem;
  left: 127.5rem;
  top: 78rem;
  font-style: normal;
  font-weight: bold;
  font-size: 2rem;
  line-height: 4rem;
  cursor: pointer;
  color: #fff;
  border: none;
  background-color: rgba(135, 113, 224, 0.89);
  border-radius: 2.5rem;
}
.contact-container .right .link {
  text-align: center;
  display: block;
  margin: 1.8rem;
  color: #71a1bd;
  font-weight: bold;
  text-decoration: none;
}
.contact-container .input {
  position: relative;
  padding: 1.35rem 1.25rem;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  column-gap: 0.75rem;
  /* Transition effect */
}
.contact-container .input .input__lock, .contact-container .input .input__icon {
  font-size: 1.25rem;
  z-index: 1;
}
.contact-container .input .input__lock {
  color: #000;
  font-size: 1.8rem;
}
.contact-container .input .input__icon {
  color: hsl(232deg, 100%, 99%);
  font-size: 1.8rem;
  cursor: pointer;
}
.contact-container .input .input__password {
  background: transparent;
  border: none;
  outline: none;
  font-size: 14px;
  z-index: 1;
}
.contact-container .input .input__password::placeholder {
  color: black;
}
.contact-container .input .input__overlay {
  width: 32px;
  height: 32px;
  background-color: hsl(232deg, 54%, 11%);
  position: absolute;
  right: 0.9rem;
  border-radius: 50%;
  z-index: 0;
  transition: 0.4s ease-in-out;
}
.contact-container .input .overlay-content {
  width: 100%;
  height: 100%;
  border-radius: 0.5rem;
  right: 0;
}
.contact-container .input .overlay-content ~ .input__lock,
.contact-container .input .overlay-content ~ .input__password,
.contact-container .input .overlay-content ~ .input__password::placeholder {
  color: hsl(232deg, 100%, 99%);
}

@media only screen and (max-width: 768px) {
  .contact-container {
    background: linear-gradient(138.56deg, #318adc 23.44%, #88183a 89.5%);
  }
  .contact-container .rigth {
    left: calc(68% - 250px);
  }
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #274690;
  color: hsl(232deg, 100%, 99%);
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.3rem 0.3rem 0.3rem 0.3rem #333;
    z-index: 1;
  }
}
.header .nav ul {
  display: flex;
  margin: 0;
  padding: 0;
}
.header .nav ul li {
  margin-right: 2rem;
}
.header .search {
  width: fit-content;
  height: fit-content;
  position: relative;
}
.header .search .input-search {
  height: 5rem;
  width: 5rem;
  border-style: none;
  padding: 1rem;
  font-size: 1.8rem;
  letter-spacing: 0.2rem;
  outline: none;
  border-radius: 2.5rem;
  transition: all 0.5s ease-in-out;
  padding-right: 4rem;
  background-color: #1B264F;
  color: #fff;
}
.header .search .input-search::placeholder {
  color: rgba(255, 255, 255, 0.5);
  font-size: 18px;
  letter-spacing: 2px;
  font-weight: 100;
}
.header .search .btn-search {
  width: 50px;
  height: 50px;
  border-style: none;
  font-size: 20px;
  font-weight: bold;
  outline: none;
  cursor: pointer;
  border-radius: 50%;
  position: absolute;
  right: 0px;
  color: #ffffff;
  background-color: transparent;
  pointer-events: painted;
}
.header .search .btn-search:focus ~ .input-search {
  width: 300px;
  border-radius: 0px;
  background-color: #576CA8;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  transition: all 500ms cubic-bezier(0, 0.11, 0.35, 2);
}
.header .search .input-search:focus {
  width: 300px;
  border-radius: 0px;
  background-color: #576CA8;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  transition: all 500ms cubic-bezier(0, 0.11, 0.35, 2);
}

.banner {
  height: 80vh;
  background-image: url(../img/hxh.avif);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.banner .banner__container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: inherit;
}
.banner .banner__container .banner__texto {
  display: block;
  margin-left: 2rem;
  height: 2rem;
  font-family: monospace;
  font-size: 2rem;
  white-space: nowrap;
  border-right: 0.4rem solid;
  width: 39ch;
  animation: typing 2s steps(39), blink 0.5s infinite step-end alternate;
  overflow: hidden;
}
@keyframes typing {
  from {
    width: 0;
  }
}
@keyframes blink {
  50% {
    border-color: transparent;
  }
}

