.banner-index {
  background-image: url(../img/hxh.png);
}

.banner-manga {
  background-image: url(../img/banner_manga.jpg);
}

.container__texto {
  display: flex;
  align-items: center;
  justify-content: center;
}
.container__texto .texto-seccion {
  display: block;
  width: 100%;
  border: solid black 0.5px;
}

.contenido-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  gap: 5rem;
  justify-items: center;
}
.contenido-grid .producto .producto__imagen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 0.5s ease-in-out;
  overflow: hidden;
}
.contenido-grid .producto .producto__imagen:hover {
  transform: scale(1.1);
}
.contenido-grid .producto .producto__imagen .border-descuento {
  text-decoration: line-through;
}
.contenido-grid .producto .producto__imagen .font-descuento {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}
.contenido-grid .producto .producto__imagen .font-precio {
  font-size: 2rem;
}
.contenido-grid .producto .producto-informacion {
  display: flex;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
}
.contenido-grid .producto .producto-informacion .btn-comprar {
  transition: all 0.5s ease-in-out;
}
.contenido-grid .producto .producto-informacion .btn-comprar:hover {
  transform: scale(1.1);
}

.contenedor-badge {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.manga {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.manga .manga-container {
  width: 80rem;
  height: 40rem;
}
.manga .manga-descripcion {
  width: 50%;
  height: 100%;
  position: relative;
}
.manga .manga-descripcion .manga-sinopsis {
  line-height: 1.5;
}
.manga .manga-descripcion .contenedor-detalle li {
  display: block;
  margin-top: 1rem;
  padding: 2rem;
  color: #fff;
}
.manga .manga-descripcion .contenedor-precio .btn-comprar {
  transition: all 0.5s ease-in-out;
}
.manga .manga-descripcion .contenedor-precio .btn-comprar:hover {
  transform: scale(1.1);
}

.bg-azul {
  background-color: #576CA8;
}
.bg-azul li:nth-child(odd) {
  background-color: #3d4c76;
}

.bg-verde {
  background-color: hsl(232deg, 8%, 35%);
}
.bg-verde li:nth-child(odd) {
  background-color: #3b3c45;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #274690;
  color: hsl(232deg, 100%, 99%);
  margin-top: 8rem;
  height: 30rem;
  background: rgb(27, 38, 79);
  background: linear-gradient(90deg, rgb(27, 38, 79) 0%, rgb(39, 70, 144) 35%, rgb(87, 108, 168) 100%);
}
.footer .nav ul {
  display: flex;
  margin: 0;
  padding: 0;
}
.footer .nav ul li {
  margin-right: 2rem;
}
.footer ul {
  margin: 0;
  padding: 0;
}