@use 'variables' as v;
html{ 
    font-size: 62.5%;
    box-sizing: border-box;
}
*,
*:before,
*:after{
    box-sizing: inherit;
}
h1, h2, h3, h4{
    font-family: v.$fuente_principal;
    line-height: 1.2;
}
h1{
    font-size: 4rem;
}
h2{
    font-size: 3rem;
}
h3{
    font-size: 2rem;
}
h4{
    font-size: 1.8rem;
}
img{
    max-width: 100%;
    display:block;
    margin-right: 0;
}
.imgH{
    height: 30rem;
}
a{
    text-decoration: none;
    color: v.$blanco;
}
li{
    list-style: none;
}
body{
    font-family: v.$fuente_titulos;
    font-size: 1.6rem;
    line-height: 1.5rem;
    &.fijar-body{
        overflow: hidden;
    }
    &.body-scroll{
        padding-top: 8.1rem;
    }
}
.btn{
    margin-top: 2rem;
    padding: 1.8rem;
    color: #fff;
    border: none;
}
.btn-comprar{
    background-color: #23d160;
    text-align: center;
    display: block;
}
.buy{
    margin-right: 1rem;
}
//Utilidades
.contenedor{
    width: min(90%, 120rem);
    margin: 0 auto;
}
.no-margin{
    margin: 0;
}
.centrar-texto{
    text-align: center;
}
.texto-right{
    text-align: right;
}
.section{
    margin-top: 8rem;
    margin-bottom: 3rem;
}
.normal{
    font-weight: normal;
}
.bold{
    font-weight: bold;
}
.heading{
    margin: 2rem 1rem;
}
.fw-300{
    font-weight: 300;
}
.fw-400{
    font-weight: 400;
}
.fw-600{
    font-weight: 600;
}
.margin-info{
    margin: 1rem 0;
}
.borde{
    border-left: 1px solid black;
    border-right: 1px solid black;
}
.badge{
    padding: 1.2rem;
    border-radius: 2.5rem;
    margin-right: 2rem;
}
.badge-disponible{
    background-color: #23d160;
    color: white;
}
.badge-no-disponible{
    background-color: v.$rojo;
    color: white;
}