@use '../base/mixins' as m;
@use '../base/variables' as v;
.header{
    @include m.tablet {
        &.fijo{
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .3rem .3rem .3rem .3rem #333;
            z-index: 1;
        }
    }
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: v.$background-header;
    color: v.$white-color;
    .nav{
        ul{
            display: flex;
            margin: 0;
            padding: 0;
            li{
                margin-right: 2rem;
            }
        }
    }
    .search{
        width: fit-content;
        height: fit-content;
        position: relative;
        .input-search{
            height: 5rem;
            width: 5rem;
            border-style: none;
            padding: 1rem;
            font-size: 1.8rem;
            letter-spacing: .2rem;
            outline: none;
            border-radius: 2.5rem;
            transition: all .5s ease-in-out;
            padding-right: 4rem;
            background-color: v.$background-search-icon;
            color: #fff;
        }
        .input-search::placeholder{
            color:rgba(255,255,255,.5);
            font-size: 18px;
            letter-spacing: 2px;
            font-weight: 100;
        }
        .btn-search{
            width: 50px;
            height: 50px;
            border-style: none;
            font-size: 20px;
            font-weight: bold;
            outline: none;
            cursor: pointer;
            border-radius: 50%;
            position: absolute;
            right: 0px;
            color:#ffffff ;
            background-color:transparent;
            pointer-events: painted;  
        }
        .btn-search:focus ~ .input-search{
            width: 300px;
            border-radius: 0px;
            background-color: v.$background-search-input;
            border-bottom:1px solid rgba(255,255,255,.5);
            transition: all 500ms cubic-bezier(0, 0.110, 0.35, 2);
        }
        .input-search:focus{
            width: 300px;
            border-radius: 0px;
            background-color: v.$background-search-input;
            border-bottom:1px solid rgba(255,255,255,.5);
            transition: all 500ms cubic-bezier(0, 0.110, 0.35, 2);
        }
    }
}
.banner{
    height: 80vh;
    background-image: url(../img/hxh.avif);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    .banner__container{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        height: inherit;
        .banner__texto{
            display: block;
            margin-left: 2rem;
            height: 2rem;
            font-family: monospace;
            font-size: 2rem;
            white-space: nowrap;
            border-right: .4rem solid;
            width: 39ch;
            animation: typing 2s steps(39),
            blink .5s infinite step-end alternate;
            overflow: hidden;
        }
        @keyframes typing {
            from{width: 0;}
        }
        @keyframes blink {
            50%{ border-color: transparent;}
        }
    }
}