@use '../base/mixins' as m;
@use '../base/variables' as v;
.footer{
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: v.$background-header;
    color: v.$white-color;
    margin-top: 8rem;
    .nav{
        ul{
            display: flex;
            margin: 0;
            padding: 0;
            li{
                margin-right: 2rem;
            }
        }
    }
    height: 30rem;
    background: rgb(27,38,79);
    background: linear-gradient(90deg, rgba(27,38,79,1) 0%, rgba(39,70,144,1) 35%, rgba(87,108,168,1) 100%);
    ul{
        margin: 0;
        padding: 0;
    }
}