@use '../base/mixins' as m;
@use '../base/variables' as v;
.container__texto{
    display: flex;
    align-items: center;
    justify-content: center;
    .texto-seccion{
        display: block;
        width: 100%;
        border: solid black .5px;
    }
}
.contenido-grid{
    @include m.gridTest(5rem);
    .producto{
        .producto__imagen{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            transition: all 0.5s ease-in-out;
            overflow: hidden;
            &:hover{
                transform: scale(1.1);
            }
            .border-descuento{
                text-decoration: line-through;
            }
            .font-descuento{
                font-size: 1.5rem;
                margin-bottom: 1rem;
            }
            .font-precio{
                font-size: 2rem;
            }
        }
        .producto-informacion{
            display: flex;
            align-items: center;
            flex-direction: column;
            flex-wrap: nowrap;
            .btn-comprar{
                transition: all 0.5s ease-in-out;
                &:hover{
                    transform: scale(1.1);
                }
            }
        }
    }
}