@use '../base/mixins' as m;
@use '../base/variables' as v;
.banner{
    height: 80vh;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    .banner__container{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        height: inherit;
        .banner__texto{
            display: block;
            margin-left: 2rem;
            height: 2rem;
            font-family: monospace;
            font-size: 2rem;
            white-space: nowrap;
            border-right: .4rem solid;
            width: 39ch;
            animation: typing 2s steps(39),
            blink .5s infinite step-end alternate;
            overflow: hidden;
        }
        @keyframes typing {
            from{width: 0;}
        }
        @keyframes blink {
            50%{ border-color: transparent;}
        }
    }
}
.banner-index{
    background-image: url(../img/hxh.png);
}
.banner-manga{
    background-image: url(../img/banner_manga.jpg);
}